#Blog {
  font-size: 1.28571429em;
  padding-left: 17%;
  padding-right: 17%;
  padding-bottom: 100px;
}
#Blog #TitleImg,
#Blog #TitleImgOverlay {
  background: center fixed;
  width: 100%;
  height: 50vh;
  background-size: cover;
  z-index: -2;
  position: absolute;
  left: 0;
}
#Blog #TitleImgOverlay {
  background: #b5ac88;
  opacity: 0.3;
  position: absolute;
  width: 100%;
  z-index: -1;
}
#Blog #TitleImgPlacholder {
  width: 100%;
  height: 55vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
}
#Blog #TitleImgPlacholder h1 {
  background-color: white;
  padding: 2px 20px;
  border-radius: 12px;
  font-size: 3.57142857em;
}
#Blog .q-carousel__navigation-inner .text-primary {
  color: white !important;
}
#BlogOverview #TopContainer {
  background-color: white;
  width: 100%;
  position: absolute;
  left: 0;
  text-align: center;
  max-width: none;
}
#BlogOverview #TopContainer .topic {
  padding: 20px 0 20px 0;
  margin: 0;
  width: 100%;
  left: 0;
}
#BlogOverview #TopContainer > p {
  font-size: 1.28571429em;
}
#BlogOverview #BlogBackgroundImgPlaceholder {
  display: flex;
  flex-direction: column;
  padding-top: 200px;
}
#BlogOverview #BlogBackgroundImg {
  background: fixed center url('../statics/travel-background.jpeg');
  width: 100%;
  height: 100vh;
  position: fixed;
  left: 0;
  background-size: cover;
  z-index: -2;
  opacity: 0.35;
  max-width: none;
}
#BlogOverview .story-card-container {
  display: flex;
  justify-content: center;
  padding: 20px 0 20px 0;
}
#BlogOverview .story-card-container .story-card {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  text-align: center;
  align-items: center;
  padding: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  transition: 0.6s;
  text-decoration: none;
  width: 90%;
  max-width: 940px;
}
#BlogOverview .story-card-container .story-card > div {
  width: 100%;
}
#BlogOverview .story-card-container .story-card .story-title {
  margin: 0 0 10px 0;
}
#BlogOverview .story-card-container .story-card .author-container {
  font-size: 1.28571429em;
  text-align: left;
}
#BlogOverview .story-card-container .story-card .author-container > p:nth-child(1) {
  margin: 0;
  font-size: 1.28571429em;
}
#BlogOverview .story-card-container .story-card .card-row {
  display: flex;
  flex-wrap: wrap;
}
#BlogOverview .story-card-container .story-card .card-row > div {
  width: 50%;
  min-width: 200px;
}
@media only screen and (min-width: 0px) and (max-width: 719px) {
  #Blog {
    padding-left: 5px;
    padding-right: 5px;
  }
  #BlogOverview {
    padding-left: 10px;
    padding-right: 10px;
  }
  #BlogOverview #TopContainer {
    padding-left: 10px;
    padding-right: 10px;
  }
  #BlogOverview #TopContainer .topic {
    font-size: 2.14285714em;
  }
  #BlogOverview .story-title {
    font-size: 2.14285714em;
  }
  #BlogOverview .card-row > div {
    padding-left: 0 !important;
  }
}
